@font-face {
    font-family: 'GmarketSansMedium';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}


a {color: inherit; text-decoration: none;}
body {font-family: 'GmarketSansMedium' !important; line-height: normal !important;}
button {background: none; border: none; padding: 0; margin: 0;}

.contentsBox {width: 100%; height: 100vh;  background: #fff; box-sizing: border-box;}
.contentsBox .contentsBox_inner {width: 100%; height: 100%; box-shadow: 0px 36px 40px rgba(0, 0, 0, 0.5); display: flex; align-items: flex-start; overflow: hidden; background:linear-gradient(-45deg, #1a7df0, #5d9aff 85%); position: relative;}

.contentsBox .contentsBox_inner .cloudIcon {display: inline-block;}
.contentsBox .contentsBox_inner .cloudIcon img {width: 100%; max-width: 260px;}
.contentsBox .contentsBox_inner .cloudIcon2 {display: inline-block;}
.contentsBox .contentsBox_inner .cloudIcon2 img {width: 100%; max-width: 130px;}

.centerContent {width: 100%; height: 100%; z-index: 99;}

.page_contents {width: 100%; height: 100%;}
.main {width: 100%; height: 100%;}
.main .main_inner {width: 100%; display: flex; align-items: flex-start; height: 100%;}
.main .main_inner .main_leftContent {width: 60%;  height: 100%; overflow-y: auto; box-sizing: border-box; padding: 20px 20px 100px; overflow-x: hidden;}

.main .main_inner .main_leftContent::-webkit-scrollbar {
    width: 10px;  
}
.main .main_inner .main_leftContent::-webkit-scrollbar-thumb {
    background: rgb(0, 86, 247);
    border-radius: 10px; 
}
.main .main_inner .main_leftContent::-webkit-scrollbar-track {
    background: rgb(93, 154, 255); 
}
.main .main_inner .main_leftContent .weatherSlide_box {width: 100%; margin-bottom: 60px;}
.main .main_inner .main_leftContent .weatherSlide_box .weather_day {display: flex; align-items: center; justify-content: center; margin-bottom: 28px;}
.main .main_inner .main_leftContent .weatherSlide_box .weather_day .day {font-size: 16px; width: 100%; max-width: 78px; text-align: center;box-sizing: border-box; margin: 0 10px; color: #fff;}
.main .main_inner .main_leftContent .weatherSlide_box .weather_day button svg {font-size: 24px; color: #fff;}
.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide {background: rgba(255, 255, 255, 0.2); border-radius: 44px;width: calc(100% - 6px); border: 1px solid #fff; margin-bottom: 120px; overflow: hidden;}
.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slider-container {width: 100%; }
.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts { box-sizing: border-box; padding: 4px; position: relative;}
.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_top {width: 100%; position: relative; border-bottom: 1px solid #fff; box-sizing: border-box; padding: 10px 26px; text-align: center; margin-bottom: 38px;}
.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_top .Shirt {position: absolute; left: 10px; top: 50%; transform: translateY(-50%); }
.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_top .Shirt svg {color: #fff; font-size: 18px; transition: all 0.2s;}
.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_top .Shirt:hover svg {color: #ddd; transition: all 0.2s;}
.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_top .time {display: inline-block; color: #fff; border-radius: 18px; box-sizing: border-box; padding: 4px 12px; font-size: 12px;}
.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .alarmBox {position: absolute; left: 0; top: 0; width: 100%; background: rgba(0, 0, 0, 0.8); height: 0; display: flex; align-items: center;justify-content: center; visibility: hidden; transition: all 0.2s;}
.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .alarmBox.on {height: 100%; visibility: visible; transition: all 0.2s;}
.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .alarmBox h2 {color: #fff; font-size: 18px; word-break: keep-all; font-size: 14px; box-sizing: border-box; padding: 0 10px; text-align: center;}
.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .alarmBox .close {position: absolute; left: 10px; top: 10px;}
.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .alarmBox .close svg {color: #fff; font-size: 24px;}

.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_center {width: 100%; text-align: center; margin-bottom: 38px;}
.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_center .weather_text {font-size: 18px;  color: #fff; margin-bottom: 40px;}
.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_center .weatherIcon {height: 146px; display: flex; align-items: center;justify-content: center;}
.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_center .weatherIcon img {width: 100%; max-width: 100px;}
.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_center .temperature {font-size: 20px; font-weight: 800; color: #fff;}


.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_bottom {width: 100%; margin-bottom: 18px;}
.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_bottom ul {width: 100%; display: grid; grid-template-columns: repeat(2, 1fr); gap: 10px 10px;}
.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_bottom ul li {width: 100%; text-align: center;}
.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_bottom ul li span {display: inline-block; box-sizing: border-box; padding: 2px 10px; border-radius: 18px; border: 1px solid #fff; font-size: 14px; color: #fff; margin-bottom: 8px;}
.main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_bottom ul li p {font-size: 14px; color: #fff;}


.main .main_inner .main_leftContent .time_box {width: 100%; text-align: center; overflow: hidden; margin: 12% 0 14%;}
.main .main_inner .main_leftContent .time_box .time {font-size: 100px; color: #fff; margin-bottom: 50px;}
.main .main_inner .main_leftContent .time_box .local {color: #fff; font-size: 24px; margin-bottom: 8px;}
.main .main_inner .main_leftContent .time_box .local svg {margin-right: 8px;}
.main .main_inner .main_leftContent .time_box .day {font-size: 20px; color: #fff;}
.main .main_inner .main_leftContent .time_box .alarm {width: 100%; max-width: 640px; border: 1px solid #fff; background: rgba(255, 255, 255, 0.3); border-radius: 24px; display: flex; align-items: center; justify-content: center; box-sizing: border-box; padding: 10px 20px; margin: 40px auto 0}
.main .main_inner .main_leftContent .time_box .alarm .alarm_text {font-size: 18px; color: #fff; word-break: keep-all; }
.main .main_inner .main_leftContent .time_box .alarm svg {color: #fff; font-size: 18px; margin-right: 4px; }


.main .main_inner .main_rightContent {width: 40%;  height: 100%; display: flex; align-items: center; justify-content: center;}
.main .main_inner .current_weather_box {width: 100%; max-width: 560px;  border-radius: 40px; box-sizing: border-box; padding: 30px; position: relative; background: rgba(255, 255, 255, 0.2); border: 1px solid #fff; margin: 0 30px;}
.main .main_inner .current_weather_box.mo {display: none;}
.main .main_inner .current_weather_box .temperature {color: #fff; font-size: 62px; margin-bottom: 58px;}
.main .main_inner .current_weather_box .weatherText {font-size: 14px; color: #fff;}
.main .main_inner .current_weather_box .info_list {display: inline-block; line-height: 1.5; position: relative; z-index: 99; }
.main .main_inner .current_weather_box .info_list li {color: #fff; font-size: 14px; }
.main .main_inner .current_weather_box .info_list li .slace {display: inline-block; margin: 0 4px;}
.main .main_inner .current_weather_box .titleIcon {position: absolute;  right: 10%; top: 10%; display: inline-block; width: auto;}
.main .main_inner .current_weather_box .titleIcon img {display: inline-block; width: auto;}
.main .main_inner .current_weather_box .titleIcon svg {color: #fff; font-size: 88px; }




@media(max-width : 900px){
    .main .main_inner .main_rightContent {display: none;}
    .main .main_inner .main_leftContent {width: 100%;}
    .main .main_inner .current_weather_box.mo {display: block; text-align: left;}
    .main .main_inner .current_weather_box {margin: 30px auto;}
    .main .main_inner .main_leftContent .time_box .alarm {display: none;}
}
@media(max-width : 720px){
    
    .main .main_inner .main_leftContent { padding: 2.7778vw 2.7778vw 13.8889vw;}
    .main .main_inner .main_leftContent .time_box {margin: 12% 0 12%;}
    .main .main_inner .main_leftContent .time_box .time{font-size: 12.5000vw; margin-bottom: 2.5000vw;}
    .main .main_inner .main_leftContent .time_box .local{font-size: 3.3333vw; margin-bottom: 0;}
    .main .main_inner .main_leftContent .time_box .local svg {margin-right: 1.1111vw;}
    .main .main_inner .main_leftContent .time_box .day {font-size: 2.7778vw;}
    .main .main_inner .current_weather_box {margin: 4.1667vw auto; max-width: 77.7778vw; border-radius: 5.5556vw; padding: 4.1667vw; max-width: unset;}
    .main .main_inner .current_weather_box .temperature {font-size: 8.6111vw; margin-bottom: 8.0556vw;}
    .main .main_inner .current_weather_box .titleIcon {right: 4.1667vw; top: 4.1667vw; width: 100%; max-width: 23.6111vw;}
    .main .main_inner .current_weather_box .titleIcon img {width: 100%; display: block;}
    .main .main_inner .current_weather_box .info_list li {font-size: 2.2222vw;}
    .main .main_inner .main_leftContent .weatherSlide_box .weather_day {margin-bottom: 3.8889vw;}
    .main .main_inner .main_leftContent .weatherSlide_box .weather_day button svg {font-size: 5.0000vw;}
    .main .main_inner .main_leftContent .weatherSlide_box .weather_day .day {font-size: 3.6111vw; max-width: 11.9444vw; margin: 0 2.5000vw;}
    .main .main_inner .main_leftContent .weatherSlide_box .weatherSlide {border-radius: 2.5000vw; margin-bottom: 16.6667vw;}
    .main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts {padding: 0.5556vw;}
    .main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_top {padding: 2.3333vw 3.6111vw; margin-bottom: 5.2778vw;}
    .main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_top .Shirt svg {font-size: 3.3333vw;}
    .main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_top .time {font-size: 2.2222vw; border-radius: 0; padding: 0;}
    .main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_center .weather_text {font-size: 2.7778vw; margin-bottom: 1.1111vw;}
    .main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_center {margin-bottom: 5.2778vw;}
    .main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_center .weatherIcon {height: 20.2778vw;}
    .main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_center .weatherIcon img {max-width: 14.4444vw;}
    .main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_center .temperature {font-size: 2.7778vw;}
    .main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_bottom ul li span {padding: 0.5556vw 1.9444vw; border-radius: 2.5000vw; font-size: 2.2222vw; margin-bottom: 1.1111vw;}
    .main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_bottom ul {gap: 1.3889vw 1.3889vw;}
    .main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .slide_bottom ul li p {font-size: 2.2222vw;}
    .main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .alarmBox .close svg {font-size: 3.8889vw;}
    .main .main_inner .main_leftContent .weatherSlide_box .weatherSlide .slide_conts .alarmBox h2 {font-size: 2.2222vw;}
    .main .main_inner .main_leftContent::-webkit-scrollbar {width: 1.3889vw; }
    .main .main_inner .main_leftContent::-webkit-scrollbar-thumb {border-radius: 1.3889vw; }
    .contentsBox .contentsBox_inner .cloudIcon img {max-width: 50.0000vw;}
    .contentsBox .contentsBox_inner .cloudIcon2 img {max-width: 30.0000vw;}


}






